@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@600&display=swap);
:root{
    --thistle: #D8C1D9;
    --gray: rgb(110, 126, 140);
    --offwhite: rgb(250, 248, 239);
    --green: #078C7F;
    --brown: #A66A65;
    /* --terracotta: #ED6A5A; */
    /* --mac: #faae7b; */
    --mac: #E9B44C;
    --metallic-seaweed: rgb(25, 139, 151);

    --mauve: rgb(58, 23, 33);
    /* --electric: rgb(9, 192, 195); */
    --violet: rgb(37, 24, 37);
    --rosedust: #9f6976;
    --middlered: rgb(238, 217, 211);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Calibri', 'Nunito Sans', sans-serif;
  background-color: rgb(250, 248, 239);
  background-color: var(--offwhite);
  color: rgb(58, 23, 33);
  color: var(--mauve);
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
      background: 
        linear-gradient(
        rgb(250, 248, 239, .7), 
        rgb(250, 248, 239, .9)),
        url(/ncbui.github.io/static/media/bg.d9cec98a.jpg) center right;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
    font-family: 'Oranienbaum', serif;
    font-weight: bold;
    /* font-size: 3rem; */
}

h2 {
    font-family: 'Oranienbaum', serif;
    font-weight: bold;
}

h3 {
    font-family: 'Calibri', 'Nunito Sans', sans-serif;
    font-weight: bold;
}


h4 {
    font-family: 'Calibri', 'Nunito Sans', sans-serif;
    min-width: -webkit-min-content;
    min-width: -moz-min-content;
    min-width: min-content;
    margin: 5vh 0;
}

h5 {
    font-family: 'Calibri', 'Nunito Sans', sans-serif;
    font-weight: bold;
}

.btn {
    background-color: rgb(37, 24, 37);
    background-color: var(--violet);
    border: 1vw solid rgb(37, 24, 37);
    border: 1vw solid var(--violet);
    margin: 0 1vw;
    box-shadow: 5px 1px 10px #888888;
    font-size: 1.1rem;
}

.card {
    background: transparent;
    border: 5px solid rgb(37, 24, 37);
    border: 5px solid var(--violet);
    display: inline-flex;
    flex-direction: row;
    padding: 5vh 1vw;
    border-radius: 0;
    box-shadow: 5px 5px 5px #888888;
}


.card-img {
    background-color: transparent;
}

.section-container {
    margin: 0;
    padding: 0;
}

a, li a {
    color: #E9B44C;
    color: var(--mac);
}

a:hover {
    text-decoration: none;
}


.section-top-left {
    max-width: 300px;
    display: relative;
    top: 0px;
    left: -100px;
    z-index: 5;
    padding: 0;
    margin: 0;
}

.section-bottom-right {
    padding-left: 200px;
    min-height: 300px;
    bottom: 20vh;
    display: relative;
    z-index: 1;
}

.section-bottom-center {
    min-height: 100px;
    z-index: 1;
    padding: 0; 
    margin: 0;
}

.App {
    margin: 0;
    padding: 0;
}


.Footer {
    background-color: var(--violet);
    color: var(--rosedust);
    bottom: 0;
    min-height: 10vh;
    padding: 3vh 1vw;
}

.Footer a {
    color: var(--mac);
}

.Footer-contact-list {
    list-style: none;
}
.Landing {
    min-height: 60vh;
    background: 
    linear-gradient(
        rgba(37, 24, 37, 0.70), 
        var(--violet));
    padding: 12vh 0;
    color: var(--offwhite);
    overflow: hidden;
}

.Landing-icon {
    color: var(--rosedust);
    margin: 0 1vw;
}

.Landing-name {
    color: var(--mac);
    display: inline;
}

.Landing .btn {
    background-color: var(--offwhite);
    color: var(--violet);
    border: 4px solid var(--offwhite);
    font-weight: bold;
    font-family: 'Calibri', 'Nunito Sans', sans-serif;
    padding: 10px;
    box-shadow: none;
}

.border:hover {
        box-shadow: inset 0 0 0 5px var(--mac);
        transition:all 0.3s ease;
}

.Landing-card {
    box-shadow: none;
    border: none;
}

.About-bottom {
    flex-direction: row;
    background-color: rgb(250, 248, 239, .7);
}

.ProjectsShort {
    background-color: rgb(250, 248, 239, .7);
    padding: 30vh 0;
    font-size: 1.4rem;
}


.ProjectsShort-container {
    display: flex;
    justify-content: space-between;
    border: none;
}

.ProjectsShort a {
    color: var(--metallic-seaweed); 
}

.ProjectsShort li {
    margin: 3vh 0;
}
/* .Contact {
    position: sticky;
    right: 70px;
    top: 200vh;
    padding: 0;
    margin: 0;
    z-index: 10;
}

*/
.Contact-icon {
    margin: 10px;
    color: var(--offwhite);
} 
.navbar {
    z-index: 5;
    font: 'Calibri';
    font-size: larger;
    margin-top: -11vh;
    background-color: var(--violet);
    padding: 20px;
    border-bottom: .8vw solid var(--mac);
    box-shadow: 5px 5px 5px #888888;
}


.nav-item a {
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
    color: var(--offwhite);
}

.nav-link {
    color: var(--mac) !important;
}

.Nav-toggler {
    background-color: var(--offwhite);
    color: var(--mac);
}

.circle:hover {
    border-radius: 50%;
    transition:all 0.3s ease;
}

.navbar-toggler:hover {
    border-radius: 50%;
    transition:all 0.3s ease;
}
